<template>
  <div class="release">
    <div class="release-header">
      <span style="font-size: 25px">采集表发放</span>
    </div>
    <el-divider />
    <div style="height: 50px; text-align: left; line-height: 50px">
      <el-radio v-model="radio" label="1" @change="toggleDatepicker(false)">手动结束收集</el-radio>
      <el-divider direction="vertical" />
      <el-radio v-model="radio" label="2" @change="toggleDatepicker(true)">开始时间-结束时间</el-radio>
    </div>
    <div style="height: 30px; text-align: left; line-height: 30px; margin-bottom: 40px" v-if="datepicker">
      <el-date-picker v-model="date" type="daterange" range-separator="至" start-placeholder="开始日期"
        end-placeholder="结束日期" v-if="datepicker" />
    </div>
    <el-divider />
    <div style="display: flex; justify-content: center">
      <el-button type="primary" style="margin-right: 10px" @click="browsePaper(paperURL)" icon="el-icon-view">
        预览
      </el-button>
      <el-button type="success" @click="release()" icon="el-icon-upload">
        发布
      </el-button>
    </div>
    <el-divider />
  </div>
</template>

<script>
import { mapActions, mapGetters, mapMutations } from 'vuex'
import Clipboard from 'clipboard'
export default {
  name: 'Release',
  data() {
    return {
      datepicker: false,
      radio: '1',
      date: '',
      tableId: this.$route.params.tableId,
      paperURL: `http://localhost:8080/customer/${this.$route.params.tableId}`,
      show: true
    }
  },
  mounted() {
    this.tableId = this.$route.params.tableId
    this._cp = new Clipboard('.copyBtn')
  },
  destroyed() {
    this._cp.destroy()
  },
  watch: {
    paperInfo() {
      
      console.log('paperInfo update')
      if (this.paperInfo.startTime != null) {
        this.datepicker = true
        this.radio = '2'
      }
      console.log("date", this.date)
    }
  },
  methods: {
    ...mapActions([
      'releaseTable'
    ]),
    toggleDatepicker(flag) {
      this.datepicker = flag
      if (!flag) {
        this.date = null
        console.log(this.date)
      }
    },
    copyURL() {
      this.$message({
        message: '复制成功!',
        type: 'success'
      })
    },
    browsePaper(paperURL) {
      console.log(paperURL)
      window.open(paperURL)
    },
    release: async function () {
      function formatDate(date) {
        let year = date.getFullYear();
        // 注意JavaScript中的月份是从0开始的，所以需要+1  
        let month = ('0' + (date.getMonth() + 1)).slice(-2);
        // 同样，日期也是从1开始的，但是通常我们需要两位数，所以需要补零  
        let day = ('0' + date.getDate()).slice(-2);
        let hours = ('0' + date.getHours()).slice(-2);
        let minutes = ('0' + date.getMinutes()).slice(-2);
        let seconds = ('0' + date.getSeconds()).slice(-2);
        // 按照你需要的格式拼接这些部分  
        return `${year}-${month}-${day}T${hours}:${minutes}:${seconds}`;
      }

      console.log(`submitTable with tableId: ${this.tableId}`)
      let begining, deadline;
      if (this.date) {
        begining = formatDate(this.date[0])
        deadline = formatDate(this.date[1])
      }
      else {
        begining = "1970-01-01T00:00:00"
        deadline = "9999-12-30T00:00:00"
      }
      let releaseForm = {
        id: this.tableId,
        beginning: begining,
        deadline: deadline,
        fill_rule: {}
      }

      // 跟更新采集表状态
      const res = await this.releaseTable(releaseForm)
      console.log(res)
      if (res && res.data && res.data.code == 0) {
        this.$message.success(`发布成功`)
        this.$router.push({
          name: 'overview',
          params: { tableId: this.tableId }
        })
      } else {
        this.$message.error(`发放采集表异常`)
      }
    }
  }
}
</script>

<style>
.paperlink {
  height: 100%;
}

.paperlink-header {
  height: 70px;
  line-height: 70px;
  text-align: left;
  padding-left: 30px;
  border-bottom: 1px groove;
}

.paperlink-URL {
  padding-left: 30px;
  text-align: left;
}
</style>